        paymentCancel: "Une erreur s'est produite pendant le paiement.",
        privacyPolicyTitle: "Politique de confidentialité",
        privacyPolicyIntro1: "tutuyoutoo.com",
        privacyPolicyIntro2: "Maud ZELLNER",
        privacyPolicyIntro3: "Type de site / application : Voyage",
        privacyPolicySubTitle1: "Le but de cette politique de confidentialité",
        privacyPolicyDesc1: "Le but de cette politique de confidentialité est d'informer les utilisateurs de notre site des données personnelles que nous recueillerons ainsi que les informations suivantes, le cas échéant : Les données personnelles que nous recueillerons, l’utilisation des données recueillies, qui a accès aux données recueillies, les droits des utilisateurs du site, la politique de cookies du site. Cette politique de confidentialité fonctionne parallèlement aux conditions générales d’utilisation de notre site.",
        privacyPolicySubTitle2: "Lois applicables",
        privacyPolicyDesc2: "Conformément au Règlement général sur la protection des données (RGPD), cette politique de confidentialité est conforme aux règlements suivants.",
        privacyPolicySubTitle3: "Les données à caractère personnel doivent être :",
        privacyPolicyDesc3: "traitées de manière licite, loyale et transparente au regard de la personne concernée (licéité, loyauté, transparence), collectées pour des finalités déterminées, explicites et légitimes, et ne pas être traitées ultérieurement d'une manière incompatible avec ces finalités; le traitement ultérieur à des fins archivistiques dans l'intérêt public, à des fins de recherche scientifique ou historique ou à des fins statistiques n'est pas considéré, conformément à l'article 89, paragraphe 1, comme incompatible avec les finalités initiales adéquates, pertinentes et limitées à ce qui est nécessaire au regard des finalités pour lesquelles elles sont traitées (minimisation des données); exactes et, si nécessaire, tenues à jour; toutes les mesures raisonnables doivent être prises pour que les données à caractère personnel qui sont inexactes, eu égard aux finalités pour lesquelles elles sont traitées, soient effacées ou rectifiées sans tarder (exactitude); conservées sous une forme permettant l'identification des personnes concernées pendant une durée n'excédant pas celle nécessaire au regard des finalités pour lesquelles elles sont traitées; les données à caractère personnel peuvent être conservées pour des durées plus longues dans la mesure où elles seront traitées exclusivement à des fins archivistiques dans l'intérêt public, à des fins de recherche scientifique ou historique ou à des fins statistiques conformément à l'article 89, paragraphe 1, pour autant que soient mises en œuvre les mesures techniques et organisationnelles appropriées requises par le règlement afin de garantir les droits et libertés de la personne concernée (limitation de la conservation); traitées de façon à garantir une sécurité appropriée des données à caractère personnel, y compris la protection contre le traitement non autorisé ou illicite et contre la perte, la destruction ou les dégâts d'origine accidentelle, à l'aide de mesures techniques ou organisationnelles appropriées (intégrité et confidentialité). Le traitement n'est licite que si, et dans la mesure où, au moins une des conditions suivantes est remplie : la personne concernée a consenti au traitement de ses données à caractère personnel pour une ou plusieurs finalités spécifiques ; le traitement est nécessaire à l'exécution d'un contrat auquel la personne concernée est partie ou à l'exécution de mesures précontractuelles prises à la demande de celle-ci le traitement est nécessaire au respect d'une obligation légale à laquelle le responsable du traitement est soumis ; le traitement est nécessaire à la sauvegarde des intérêts vitaux de la personne concernée ou d'une autre personne physique ; le traitement est nécessaire à l'exécution d'une mission d'intérêt public ou relevant de l'exercice de l'autorité publique dont est investi le responsable du traitement ; le traitement est nécessaire aux fins des intérêts légitimes poursuivis par le responsable du traitement ou par un tiers, à moins que ne prévalent les intérêts ou les libertés et droits fondamentaux de la personne concernée qui exigent une protection des données à caractère personnel, notamment lorsque la personne concernée est un enfant. Pour les résidents de l’État de Californie, cette politique de confidentialité vise à se conformer à la California Consumer Privacy Act (CCPA). S’il y a des incohérences entre ce document et la CCPA, la législation de l’État s’appliquera. Si nous constatons des incohérences, nous modifierons notre politique pour nous conformer à la loi pertinente.",
        privacyPolicySubTitle4: "Consentement",
        privacyPolicyDesc4: "Les utilisateurs conviennent qu’en utilisant notre site, ils consentent aux conditions énoncées dans la présente politique de confidentialité et la collecte, l’utilisation et la conservation des données énumérées dans la présente politique.",
        privacyPolicySubTitle5: "Données personnelles que nous collectons",
        privacyPolicyDesc5: "Données collectées automatiquement : Nous ne collectons aucune donnée automatiquement sur notre site. Données recueillies de façon non automatique. Nous pouvons également collecter les données suivantes lorsque vous effectuez certaines fonctions sur notre site : Prénom et nom, Date de naissance, Email, Numéro de téléphone. Ces données peuvent être recueillies au moyen des méthodes suivantes : Enregistrement d'un compte, Modification d’un compte. Veuillez noter que nous ne collectons que les données qui nous aident à atteindre l’objectif énoncé dans cette politique de confidentialité. Nous ne recueillerons pas de données supplémentaires sans vous en informer au préalable.",
        privacyPolicySubTitle6: "Comment nous utilisons les données personnelles",
        privacyPolicyDesc6: "Les données personnelles recueillies sur notre site seront utilisées uniquement aux fins précisées dans la présente politique ou indiquées sur les pages pertinentes de notre site. Nous n’utiliserons pas vos données au-delà de ce que nous divulguerons. Les données que nous recueillons lorsque l’utilisateur exécute certaines fonctions peuvent être utilisées aux fins suivantes : Communication, Fonctionnement de l'application.",
        privacyPolicySubTitle7: "Avec qui nous partageons les données personnelles",
        privacyPolicyDesc7: "Employés. Nous pouvons divulguer à tout membre de notre organisation les données utilisateur dont il a raisonnablement besoin pour réaliser les objectifs énoncés dans la présente politique.",
        privacyPolicySubTitle8: "Tierces parties",
        privacyPolicyDesc8: "Nous pouvons partager les données utilisateur avec les tiers suivants : Les données ne sont pas partagées en dehors de l’application. Les tiers ne seront pas en mesure d’accéder aux données des utilisateurs au-delà de ce qui est raisonnablement nécessaire pour atteindre l’objectif donné.",
        privacyPolicySubTitle9: "Autres divulgations",
        privacyPolicyDesc9: "Nous nous engageons à ne pas vendre ou partager vos données avec d'autres tiers, sauf dans les cas suivants : si la loi l'exige, si elle est requise pour toute procédure judiciaire, pour prouver ou protéger nos droits légaux, à des acheteurs ou des acheteurs potentiels de cette société dans le cas où nous cherchons à la vendre la société. Si vous suivez des hyperliens de notre site vers un autre site, veuillez noter que nous ne sommes pas responsables et n’avons pas de contrôle sur leurs politiques et pratiques de confidentialité.",
        privacyPolicySubTitle10: "Combien de temps nous stockons les données personnelles",
        privacyPolicyDesc10: "Nous ne conservons pas les données des utilisateurs au-delà de ce qui est nécessaire pour atteindre les fins pour lesquelles elles sont recueillies.",
        privacyPolicySubTitle11: "Comment nous protégeons vos données personnelles",
